// Libs
import React, { Component } from 'react';
// Components
import ActionCard from './actioncard.js';
import actioncards from './actioncards.js';
// styles
// import styles from '../../static/css/adoptly.css';

const renderActionCards = (homeCards, length) => {
	const actions = homeCards.map((action) => {
		return (
			<div className="col-md-4" key={action.id}>
				<ActionCard
					title={action.title}
					desc={action.description}
					rte={action.rte}
					/>
			</div>
		);
	});
	const chunkCards = (arr, size) => {
		let rows = [];
		for (let i = 0; i < arr.length; i+=size) {
			rows.push(arr.slice(i,i+size));
		}
		return rows;
	};
	const renderRows = () => {
		// let count = 0;
		return (
			chunkCards(actions, 3).map((row, index) => {
				return (
					<div className="row" key={index}>
						{ row }
					</div>
				);
			})
		);
	};
	return (
		renderRows()
	);
};

export default class HomeSupporting extends Component {
	render() {
		const homeActionCards = actioncards.Home;
		return (
			<div className={['supporting'].join(' ')}>
				<div className={['container'].join(' ')}>
					{ renderActionCards(homeActionCards, homeActionCards.length) }
				</div>
			</div>
		);
	}
}

