let count = 0;
const ActionCards = {
	'Home':[
		{
			title: 'Search',
			description: 'Find a pet based on location, type, breed, age, and gender. We look through hundreds of shelters, agencies, and rescue sites.',
			rte: '/animals',
			id: count++
		},
		{
			title: 'Learn',
			description: 'Read articles on feeding, training, grooming, and more. Our resources ensure that you are fully prepared as a new pet owner.',
			rte: '/blog',
			id: count++
		},
		{
			title: 'Help',
			description: 'Get involved to help pets in need. You can make a difference through volunteering, social media, and donations.',
			rte: '/events',
			id: count++
		},
	],
};

export default ActionCards;



