// Libs
import React, { Component } from 'react';
// Components
import HomeSupporting from './HomeSupporting.js';
// styles
// import styles from '../../static/css/adoptly.css';


export default class Home extends Component {
	render() {
		return(
			<div>
				<div className={['jumbotron'].join(' ')}>
					<div className={['container'].join(' ')}>
						<h1>Meet your new best friend.</h1>
					</div>
				</div>
				<HomeSupporting />
			</div>
		);
	}
}



